@import url('https://fonts.googleapis.com/css?family=Share+Tech+Mono');
@import url('https://rsms.me/inter/inter-ui.css');

body {
    background-color: #21252b;
}

.App {
    text-align: center;
    font-family: "Inter UI", sans-serif;
    margin-top: 5em;
}

.welcome-display {
    font-size: 3em;
    color: #fff;
    text-shadow: 0px 0px 5px #000;
}

.time-display {
    margin-top: 1em;
    font-size: 8em;
    color: #fff;
    text-shadow: 0px 0px 5px #000;
}

.sites-display {
    text-align: center;
}

.site {
    color: #fff;
    text-shadow: 0px 0px 5px #000;
    font-size: 2em;
    padding: 2em;
}

a {
    color: inherit;
    text-decoration: none;
}

.link:hover {
    color: #E06C75;
    cursor: pointer;
}

.sites-table {
    margin: 3em auto;
}


